<template>
  <div class="modal wrapper">
    <div class="overlay"></div>
    <div class="modal-content">
      <div class="bird-info">
        <div class="img-wrapper">
          <img
            v-if="currentBird.img"
            :src="`/images/known-bird-imgs/${currentBird.img}`"
          />
        </div>
        <span class="title">{{ currentBird.name }}</span>
        <button class="close icon" @click="closeModal">
          <img src="/images/icons/close.svg" />
        </button>
      </div>
      <div class="seen-heard-btns">
        <button
          :class="{ active: birdSeenOrHeard === 'seen' }"
          @click="seenOrHeard('seen')"
        >
          <span class="icon"><img src="/images/icons/eye.svg"/></span>
          Seen
        </button>
        <button
          :class="{ active: birdSeenOrHeard === 'heard' }"
          @click="seenOrHeard('heard')"
        >
          <span class="icon"><img src="/images/icons/ear.svg"/></span>
          Heard
        </button>
      </div>

      <div class="counter" v-if="birdSeenOrHeard">
        <div class="row">
          <div class="distance"></div>
          <div class="min">
            Minimum
          </div>
          <div class="max">
            Maximum
          </div>
        </div>
        <DistanceCountRow
          :type="birdSeenOrHeard"
          distance="close"
          distanceEndsInMeters="5m"
          :min="currentBirdData[birdSeenOrHeard]['close']['min']"
          :max="currentBirdData[birdSeenOrHeard]['close']['max']"
        />
        <DistanceCountRow
          :type="birdSeenOrHeard"
          distance="near"
          distanceEndsInMeters="20m"
          :min="currentBirdData[birdSeenOrHeard]['near']['min']"
          :max="currentBirdData[birdSeenOrHeard]['near']['max']"
        />
        <DistanceCountRow
          :type="birdSeenOrHeard"
          distance="mid"
          distanceEndsInMeters="80m"
          :min="currentBirdData[birdSeenOrHeard]['mid']['min']"
          :max="currentBirdData[birdSeenOrHeard]['mid']['max']"
        />
        <DistanceCountRow
          :type="birdSeenOrHeard"
          distance="far"
          distanceEndsInMeters="320m"
          :min="currentBirdData[birdSeenOrHeard]['far']['min']"
          :max="currentBirdData[birdSeenOrHeard]['far']['max']"
        />
        <DistanceCountRow
          :type="birdSeenOrHeard"
          distance="distant"
          :min="currentBirdData[birdSeenOrHeard]['distant']['min']"
          :max="currentBirdData[birdSeenOrHeard]['distant']['max']"
        />

        <label for="notes">Notes</label>
        <textarea id="notes" @blur="addNotes"></textarea>
      </div>
    </div>
    <div class="footer">
      <button @click="closeModal">
        <span class="icon"><img src="/images/icons/arrow-back.svg"/></span> Back
        to bird list
      </button>
    </div>
  </div>
</template>

<script>
import DistanceCountRow from "./DistanceCountRow";

export default {
  name: "BirdModal.vue",
  components: { DistanceCountRow },
  props: {
    currentBird: {
      type: Object,
      required: true
    },
    birdData: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      birdSeenOrHeard: null
    };
  },
  methods: {
    seenOrHeard(observationType) {
      this.birdSeenOrHeard = observationType;
    },
    closeModal() {
      this.$emit("view-change", "BirdCount");
    },
    addNotes(e) {
      this.$emit("add-observation-notes", e.target.value, this.birdSeenOrHeard);
    }
  },
  computed: {
    currentBirdData: {
      get() {
        // get the current bird in the model object that we're passing down from the app
        return this.birdData[this.currentBird.name];
      }
    }
  }
};
</script>

<style lang="less" scoped>
@import "../styles/variables";

.overlay {
  z-index: 1;
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.7);
}

.modal-content,
.footer {
  background: #fff;
  z-index: 3;
  margin-left: 1rem;
  width: calc(100vw - 2rem);
}

.modal-content {
  margin-top: 1rem;
  height: calc(100vh - (2rem + 54px));
  overflow: auto;
  padding: 1.2rem 1.6rem 0;
}

.footer {
  bottom: 1rem;
  button {
    color: @highlightColor;
    background: transparent;
    height: 100%;
  }
}

.bird-info {
  padding: 0.6rem 0 1rem;
  .img-wrapper {
    height: 60px;
    margin-right: 0.8rem;
    img {
      height: 60px;
    }
  }
  .close {
    background: transparent;
    opacity: 0.2;
    margin: -2rem -1rem 0 auto;
  }
}

.seen-heard-btns {
  text-align: center;
  margin-bottom: 2rem;
  z-index: 3;
  .active {
    background: @highlightColor;
    color: @textColorLight;
    img {
      filter: invert();
    }
  }
  button:first-of-type {
    border-radius: 4px 0 0 4px;
    border-right: 1px solid #ccc;
  }
  button:last-of-type {
    border-radius: 0 4px 4px 0;
  }
}

.counter {
  margin-bottom: 2.4rem;
}

.min,
.max {
  text-transform: capitalize;
  font-weight: 500;
  margin-bottom: 1rem;
}
</style>
