<template>
  <div class="modal wrapper">
    <h1>Time's up!</h1>
    <p>Five minutes elapsed</p>
    <button @click="enterCountMetadata">Enter count metadata</button>
    <button @click="continueCount" class="link">
      Finish entering observation data
    </button>
  </div>
</template>

<script>
export default {
  name: "FiveMinutesElapsed",
  methods: {
    enterCountMetadata() {
      this.$emit("view-change", "CountMetadata");
    },
    continueCount() {
      this.$emit("continue-count", "");
    }
  }
};
</script>

<style lang="less" scoped>
@import "../styles/variables";

.modal {
  background: @highlightColor;
  min-height: 100vh;
  color: @textColorLight;
  width: 100vw;
  top: 0;
  text-align: center;
  h1 {
    font-weight: 500;
    margin: 0;
  }
  p {
    margin-bottom: 2.4rem;
  }
  .link {
    margin-top: 2rem;
    background: transparent;
    color: #fff;
    font-size: 1.4rem;
    text-decoration: underline;
    font-weight: 400;
    opacity: 0.9;
  }
}
</style>
