<template>
  <div id="app">
    <transition name="component-fade" mode="out-in">
      <SetupCount
        v-if="state === 'SetupCount'"
        :currentDateAndTime="currentDateAndTime"
        @view-change="changeView"
        @add-setup-info="addSetupInfo"
      />
      <BirdCount
        :timeElapsed="timeElapsed"
        :bird-data="countData.observedBirds"
        :continuedAfterFiveMinutes="continuedAfterFiveMinutes"
        v-else-if="state === 'BirdCount'"
        @view-change="changeView"
      />
      <CountMetadata
        v-else-if="state === 'CountMetadata'"
        @finish-count="finishCount"
      />
    </transition>
    <BirdModal
      v-if="enteringBirdData"
      :bird-data="countData.observedBirds"
      @view-change="changeView"
      @add-bird-observation="addObservation"
      @add-observation-notes="addObservationNotes"
      :current-bird="currentBird"
    />
    <FiveMinutesElapsed
      v-if="
        fiveMinutesElapsed &&
          !continuedAfterFiveMinutes &&
          state !== 'CountMetadata'
      "
      @view-change="changeView"
      @continue-count="continueCount"
    />
  </div>
</template>

<script>
import Vue from "vue";
import vSelect from "vue-select";

import SetupCount from "./components/SetupCount.vue";
import BirdCount from "./components/BirdCount.vue";
import BirdModal from "./components/BirdModal.vue";
import countData from "./model.js";
import FiveMinutesElapsed from "./components/FiveMinutesElapsed";
import CountMetadata from "./components/CountMetadata";

import { saveAs } from "file-saver";

import "vue-select/dist/vue-select.css";

Vue.component("v-select", vSelect);

export default Vue.extend({
  name: "app",
  data() {
    return {
      currentDateAndTime: null,
      countStartDateAndTime: null,
      fiveMinutesElapsed: false,
      continuedAfterFiveMinutes: false,
      enteringBirdData: false,
      timeElapsed: "00:00",
      countData: countData,
      state: "SetupCount",
      currentBird: null
    };
  },
  components: {
    CountMetadata,
    FiveMinutesElapsed,
    SetupCount,
    BirdCount,
    BirdModal
  },
  methods: {
    changeView(view, bird) {
      if (bird) {
        this.enteringBirdData = true;
        this.addOrEditBird(bird);
      } else {
        this.enteringBirdData = false;
        this.state = view;
      }
    },
    addSetupInfo(date, time, duration, location) {
      this.countStartDateAndTime = new Date();
      this.countData.metadata.date = date;
      this.countData.metadata.time = time;
      this.countData.metadata.duration = duration;
      this.countData.metadata.location = location;
      this.saveToLocalStorage();
    },
    addOrEditBird(bird) {
      // check if the bird was already added
      if (!this.countData.observedBirds[bird.name]) {
        // bird doesn't exist yet
        Vue.set(this.countData.observedBirds, bird.name, {
          birdName: bird.name,
          seen: {
            close: {
              min: 0,
              max: 0
            },
            near: {
              min: 0,
              max: 0
            },
            mid: {
              min: 0,
              max: 0
            },
            far: {
              min: 0,
              max: 0
            },
            distant: {
              min: 0,
              max: 0
            }
          },
          heard: {
            close: {
              min: 0,
              max: 0
            },
            near: {
              min: 0,
              max: 0
            },
            mid: {
              min: 0,
              max: 0
            },
            far: {
              min: 0,
              max: 0
            },
            distant: {
              min: 0,
              max: 0
            }
          }
        });
      }
      this.currentBird = bird;
    },
    addObservation(type, distance, minOrMax, value) {
      let min = this.countData.observedBirds[this.currentBird.name][type][
        distance
      ]["min"];
      let max = this.countData.observedBirds[this.currentBird.name][type][
        distance
      ]["max"];

      // increase the maximum number if the minimum is equal to the maximum
      if (minOrMax === "min" && max <= min) {
        this.countData.observedBirds[this.currentBird.name][type][distance][
          "min"
        ] = value;
        this.countData.observedBirds[this.currentBird.name][type][distance][
          "max"
        ] = value;
        // set the minimum of birds to 1 if the maximum is higher than 0
      } else if (minOrMax === "max" && min === 0) {
        this.countData.observedBirds[this.currentBird.name][type][distance][
          "min"
        ] = 1;
        this.countData.observedBirds[this.currentBird.name][type][distance][
          "max"
        ] = value;
      } else {
        this.countData.observedBirds[this.currentBird.name][type][distance][
          minOrMax
        ] = value;
      }

      // if the minimum is reset to 0, set the max to 0
      if (minOrMax === "min" && value === 0) {
        this.countData.observedBirds[this.currentBird.name][type][distance][
          "max"
        ] = 0;
      }

      // if the maximum is reset to 0, set the min to 0
      if (minOrMax === "max" && value === 0) {
        this.countData.observedBirds[this.currentBird.name][type][distance][
          "min"
        ] = 0;
      }

      this.saveToLocalStorage();
    },
    addObservationNotes(notes, seenOrHeard) {
      this.countData.observedBirds[this.currentBird.name][
        seenOrHeard
      ].notes = notes;
      this.saveToLocalStorage();
    },
    continueCount() {
      this.continuedAfterFiveMinutes = true;
    },
    clearEmptyObservations() {
      for (let [currentBirdName, currentBirdData] of Object.entries(
        this.countData.observedBirds
      )) {
        let seen = currentBirdData.seen;
        let heard = currentBirdData.heard;

        let min = 0;
        let max = 0;

        let seenAndHeard = Object.values(seen).concat(Object.values(heard));

        for (const item of seenAndHeard) {
          if (item.hasOwnProperty("min") && item.hasOwnProperty("max")) {
            min += item.min;
            max += item.max;
          }
        }

        if (min === 0 && max === 0) {
          Vue.delete(this.countData.observedBirds, currentBirdName);
        }
      }
    },
    saveToLocalStorage() {
      let keyID = `${this.countData.metadata.location}_${this.countData.metadata.date}_${this.countData.metadata.time}`;
      let dataToSave = JSON.stringify(this.countData, null, "\t");

      localStorage.setItem(keyID, dataToSave);
    },
    finishCount(metadata) {
      this.countData.metadata = { ...this.countData.metadata, ...metadata };
      this.clearEmptyObservations();
      this.saveToLocalStorage();
      let fileContent = window.localStorage.getItem(
        `${this.countData.metadata.location}_${this.countData.metadata.date}_${this.countData.metadata.time}`
      );
      let file = new File(
        [fileContent],
        `${
          this.countData.metadata.location
        }_${this.countData.metadata.date.replace(
          /\//g,
          "-"
        )}_${this.countData.metadata.time.replace(/:/g, "-")}.json`,
        { type: "text/plain;charset=utf-8" }
      );
      saveAs(file);
      this.resetCount();
    },
    resetCount() {
      window.location.reload();
    }
  },
  created() {
    this.currentDateAndTime = new Date();
    setInterval(() => {
      this.currentDateAndTime = new Date();

      if (this.countStartDateAndTime !== null) {
        let fiveMinutesInMilliseconds = 1000 * 60 * 5;
        //let fiveMinutesInMilliseconds = 10000;

        let timeElapsedInMilliseconds =
          this.currentDateAndTime - this.countStartDateAndTime;

        let timeElapsedDate = new Date(timeElapsedInMilliseconds);

        let timeElapsedMinutes = String(
          timeElapsedDate.getUTCMinutes()
        ).padStart(2, "0");
        let timeElapsedSeconds = String(
          timeElapsedDate.getUTCSeconds()
        ).padStart(2, "0");

        this.timeElapsed = timeElapsedMinutes + ":" + timeElapsedSeconds;

        if (
          timeElapsedInMilliseconds >= fiveMinutesInMilliseconds &&
          this.continuedAfterFiveMinutes !== true
        ) {
          this.fiveMinutesElapsed = true;
        }
      }
    }, 1000);
  }
});
</script>

<style scoped>
.component-fade-enter-active {
  transition: opacity 0.1s linear;
}
.component-fade-enter {
  opacity: 0;
}
</style>
