<template>
  <div class="wrapper">
    <div class="header">
      <span class="title">5m bird count</span>
      <span class="time-elapsed">{{ timeElapsed }}</span>
    </div>
    <div class="content">
      <div>
        <v-select
          :options="allBirds"
          placeholder="Add birds to list"
          ref="birdSelect"
          @input="addBirdToList"
        />
      </div>
      <div
        class="bird-info bird-list-item"
        v-for="bird in knownBirds"
        :key="bird.name"
        @click="addBirdData(bird)"
      >
        <div class="img-wrapper">
          <img v-if="bird.img" :src="`/images/known-bird-imgs/${bird.img}`" />
        </div>
        <span class="title">{{ bird.name }}</span>
        <span class="numbers">{{ getBirdCount(bird.name) }}</span>
      </div>
    </div>
    <div v-if="continuedAfterFiveMinutes" class="footer">
      <button @click="enterCountMetadata" class="primary">Finish Count</button>
    </div>
  </div>
</template>

<script>
import knownBirdJson from "../data/known-birds.json";
import allNZBirds from "../data/all-birds.js";

export default {
  name: "BirdCount",
  props: {
    timeElapsed: {
      type: String,
      required: true
    },
    birdData: {
      type: Object,
      required: true
    },
    continuedAfterFiveMinutes: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      knownBirds: knownBirdJson,
      allBirds: allNZBirds
    };
  },
  methods: {
    addBirdData(bird) {
      this.$emit("view-change", "BirdModal", bird);
    },
    addBirdToList(bird) {
      if (bird !== null) {
        // check that the bird wasn't added already
        let currentBird = {
          img: "",
          name: bird
        };
        if (!this.knownBirds.find(knownBirdsEl => knownBirdsEl.name === bird)) {
          this.knownBirds.push(currentBird);
          this.knownBirds.sort(function(a, b) {
            if (a.name < b.name) {
              return -1;
            }
            if (a.name > b.name) {
              return 1;
            }
            return 0;
          });
        }
        this.addBirdData(currentBird);
        this.$refs.birdSelect.clearSelection();
      }
    },
    getBirdCount(birdName) {
      if (this.birdData[birdName]) {
        let currentBird = this.birdData[birdName];
        let seen = currentBird.seen;
        let heard = currentBird.heard;

        let min = 0;
        let max = 0;

        let seenAndHeard = Object.values(seen).concat(Object.values(heard));

        for (const item of seenAndHeard) {
          if (item.hasOwnProperty("min") && item.hasOwnProperty("max")) {
            min += item.min;
            max += item.max;
          }
        }
        if (min === 0) {
          return "";
        } else {
          return `${min} - ${max}`;
        }
      }
      return "";
    },
    enterCountMetadata() {
      this.$emit("view-change", "CountMetadata");
    }
  }
};
</script>

<style lang="less" scoped>
@import "../styles/variables";

.time-elapsed {
  margin-left: 0;
  font-size: 1.4rem;
  position: fixed;
  right: 1rem;
}

.v-select {
  margin: 1rem 0;
}

.bird-info {
  border-bottom: 1px solid #eee;
  &:active {
    background: @btnBgColor;
  }
  .img-wrapper {
    width: 80px;
    margin: 8px 5px 8px 0;
    height: 50px;
    img {
      height: 50px;
    }
  }
}

.footer {
  position: fixed;
  bottom: 0;
  button {
    background: @highlightColor;
  }
}
</style>
