<template>
  <div class="wrapper">
    <div class="header">
      <span class="title">Get things ready</span>
    </div>
    <div class="content">
      <form @submit.prevent="startCount">
        <div class="field-wrapper">
          <label for="current-time">
            Start time
          </label>
          <span id="current-time">
            {{ currentTime() }}
          </span>
        </div>

        <div class="field-wrapper">
          <label for="duration">
            Duration
          </label>
          <span id="duration">
            5 minutes
          </span>
        </div>

        <div class="field-wrapper">
          <label for="location">
            Location
          </label>
          <input type="text" id="location" v-model="location" />
        </div>

        <button type="submit" :disabled="!formIsValid">Start count</button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "SetupCount",
  props: {
    currentDateAndTime: {
      type: Date,
      required: true
    }
  },
  data() {
    return {
      date: null,
      time: null,
      location: null
    };
  },
  methods: {
    currentTime() {
      let currentDate = this.currentDateAndTime;

      // get the info we need from the date

      let day = currentDate.getDate();
      let month = currentDate.getMonth();
      let year = currentDate.getFullYear();

      let hour = currentDate.getHours();
      let minute = currentDate.getMinutes();
      let second = currentDate.getSeconds();

      // format the date values and write them in the format we want

      day = String(day).padStart(2, "0");
      month = String(month + 1).padStart(2, "0");

      hour = String(hour).padStart(2, "0");
      minute = String(minute).padStart(2, "0");
      second = String(second).padStart(2, "0");

      this.time = `${hour}:${minute}:${second}`;
      this.date = `${day}/${month}/${year}`;

      return hour + ":" + minute;
    },
    startCount() {
      this.$emit("add-setup-info", this.date, this.time, "5", this.location);
      this.$emit("view-change", "BirdCount");
    }
  },
  computed: {
    formIsValid: {
      get() {
        let isValid = false;
        if (this.location !== null && this.location !== "") {
          isValid = true;
        }
        return isValid;
      }
    }
  }
};
</script>

<style lang="less" scoped>
@import "../styles/variables";

.wrapper {
  background: @highlightColor;
  color: @textColorLight;
}

.content {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  text-align: center;
}

span,
input {
  display: block;
  font-size: 2.4rem;
  font-weight: 500;
}

input {
  color: @textColorLight;
  max-width: 200px;
  text-align: center;
  &:focus {
    border-color: @fieldBorderColorLight;
  }
}
</style>
