<template>
  <div class="wrapper">
    <div class="header">
      <span class="title">Add bird count metadata</span>
    </div>
    <div class="content">
      <form @submit.prevent="addMetadata">
        <div class="field-wrapper">
          <label for="temperature">
            Temperature
          </label>
          <select id="temperature" v-model="temperature">
            <option disabled selected></option>
            <option
              v-for="item in temperatureValues"
              :value="item.value"
              :key="item.value"
              >{{ item.label }}</option
            >
          </select>
        </div>

        <div class="field-wrapper">
          <label for="wind">
            Wind
          </label>
          <select id="wind" v-model="wind">
            <option disabled selected></option>
            <option
              v-for="item in windValues"
              :value="item.value"
              :key="item.value"
              >{{ item.label }}</option
            >
          </select>
        </div>

        <div class="field-wrapper">
          <label for="noise">
            Noise
          </label>
          <select id="noise" v-model="noise">
            <option disabled selected></option>
            <option
              v-for="item in noiseValues"
              :value="item.value"
              :key="item.value"
              >{{ item.label }}</option
            >
          </select>
        </div>

        <div class="field-wrapper">
          <label for="sun">
            Sun (in minutes)
          </label>
          <select id="sun" v-model="sun">
            <option disabled selected></option>
            <option v-for="item in sunValues" :value="item" :key="item">{{
              item
            }}</option>
          </select>
          <!-- <input id="sun" type="number" min="0" max="5" v-model="sun" />-->
        </div>

        <div class="field-wrapper">
          <label for="precipitation-type">
            Precipitation type
          </label>
          <select id="precipitation-type" v-model="precipitationType">
            <option disabled selected></option>
            <option
              v-for="item in precipitationTypeValues"
              :value="item.value"
              :key="item.value"
              >{{ item.label }}</option
            >
          </select>
        </div>

        <div
          v-if="precipitationType && precipitationType !== 'N'"
          class="field-wrapper"
        >
          <label for="precipitation-value">
            Precipitation value
          </label>
          <select id="precipitation-value" v-model="precipitationValue">
            <option disabled selected></option>
            <option
              v-for="item in precipitationValueValues"
              :value="item.value"
              :key="item.value"
              >{{ item.label }}</option
            >
          </select>
        </div>

        <div class="field-wrapper">
          <label for="notes">Notes</label>
          <textarea id="notes" v-model="notes"></textarea>
        </div>

        <button type="submit" class="primary" :disabled="!formIsValid">
          Finish count
        </button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "CountMetadata",
  data() {
    return {
      temperature: null,
      wind: null,
      noise: null,
      sun: null,
      precipitationType: null,
      precipitationValue: null,
      notes: null,
      temperatureValues: [
        {
          value: 1,
          label: "Freezing (below 0 C)"
        },
        {
          value: 2,
          label: "Cold (0-5 C)"
        },
        {
          value: 3,
          label: "Cool (6-10 C)"
        },
        {
          value: 4,
          label: "Mild (11-15 C)"
        },
        {
          value: 5,
          label: "Warm (16-22 C)"
        },
        {
          value: 6,
          label: "Hot (more than 22 C)"
        }
      ],
      windValues: [
        {
          value: 0,
          label: "Leaves still or move without noise"
        },
        {
          value: 1,
          label: "Leaves rustle"
        },
        {
          value: 2,
          label: "Leaves and branches in constant motion"
        },
        {
          value: 3,
          label: "Branches or trees sway"
        }
      ],
      noiseValues: [
        {
          value: 0,
          label: "Not important"
        },
        {
          value: 1,
          label: "Moderate"
        },
        {
          value: 2,
          label: "Loud"
        }
      ],
      sunValues: [1, 2, 3, 4, 5],
      precipitationTypeValues: [
        {
          value: "N",
          label: "None"
        },
        {
          value: "M",
          label: "Mist"
        },
        {
          value: "R",
          label: "Rain"
        },
        {
          value: "H",
          label: "Hail"
        },
        {
          value: "S",
          label: "Snow"
        }
      ],
      precipitationValueValues: [
        // name of the year award
        {
          value: 0,
          label: "None"
        },
        {
          value: 1,
          label: "Dripping foliage"
        },
        {
          value: 2,
          label: "Drizzle"
        },
        {
          value: 3,
          label: "Light"
        },
        {
          value: 4,
          label: "Moderate"
        },
        {
          value: 5,
          label: "Heavy"
        }
      ]
    };
  },
  methods: {
    addMetadata() {
      if (this.precipitationValue === null) {
        this.precipitationValue = "N";
      }

      let observationMetadata = {
        temperature: this.temperature,
        wind: this.wind,
        noise: this.noise,
        sun: this.sun,
        precipitationType: this.precipitationType,
        precipitationValue: this.precipitationValue
      };

      if (this.notes) {
        observationMetadata.notes = this.notes;
      }

      this.$emit("finish-count", observationMetadata);
    }
  },
  computed: {
    formIsValid: {
      get() {
        let isValid = false;
        if (
          this.temperature !== null &&
          this.temperature !== "" &&
          this.wind !== null &&
          this.wind !== "" &&
          this.noise !== null &&
          this.noise !== "" &&
          this.sun !== null &&
          this.sun !== "" &&
          this.precipitationType !== null &&
          this.precipitationType !== "" &&
          (this.precipitationType === "N" ||
            (this.precipitationValue !== null &&
              this.precipitationValue !== ""))
        ) {
          isValid = true;
        }
        return isValid;
      }
    }
  }
};
</script>

<style lang="less" scoped>
.content {
  padding-top: 1.6rem;
}
button {
  width: 100%;
  margin-bottom: 1.6rem;
}
</style>
