<template>
  <div class="row">
    <div class="distance">
      <span class="distance-title">{{ distance }}</span>
      <span class="distance-ends">{{ distanceEndsInMeters }}</span>
    </div>
    <div class="min">
      <button
        v-if="minState === 'button'"
        @touchstart="startTimer('min')"
        @click="addBirdObservation(distance, 'min', min + 1)"
      >
        {{ min }}
      </button>
      <input
        v-else
        @blur="minState = 'button'"
        @change="e => updateValue(e, 'min')"
        @keypress.enter="minState = 'button'"
        ref="inputField"
        type="number"
        :value="min"
      />
    </div>
    <div class="max">
      <button
        v-if="maxState === 'button'"
        @touchstart="startTimer('max')"
        @click="addBirdObservation(distance, 'max', max + 1)"
      >
        {{ max }}
      </button>
      <input
        v-else
        @blur="maxState = 'button'"
        @change="e => updateValue(e, 'max')"
        @keypress.enter="maxState = 'button'"
        ref="inputField"
        type="number"
        :value="max"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: "DistanceCountRow",
  props: {
    type: {
      type: String,
      required: true
    },
    distance: {
      type: String,
      required: true
    },
    distanceEndsInMeters: {
      type: String,
      required: false
    },
    min: {
      type: Number,
      required: true
    },
    max: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      longPressTimer: null,
      minState: "button",
      maxState: "button"
    };
  },
  methods: {
    addBirdObservation(distance, minOrMax, value) {
      this.$parent.$emit(
        "add-bird-observation",
        this.type,
        distance,
        minOrMax,
        value
      );
    },
    startTimer(buttonType) {
      let clearMouseDownListener = () => {
        clearTimeout(this.longPressTimer);
        window.removeEventListener("mouseup", clearMouseDownListener);
      };

      this.longPressTimer = setTimeout(() => {
        if (buttonType === "min") {
          this.minState = "input";
        } else {
          this.maxState = "input";
        }
        window.removeEventListener("mouseup", clearMouseDownListener);
      }, 700);
      window.addEventListener("mouseup", clearMouseDownListener);
    },
    updateValue(e, minOrMax) {
      this.addBirdObservation(this.distance, minOrMax, Number(e.target.value));
    }
  },
  updated() {
    if (this.$refs.inputField) {
      this.$refs.inputField.focus();
      this.$refs.inputField.select();
    }
  }
};
</script>

<style lang="less" scoped>
@import "../styles/variables";

.row {
  margin-bottom: 1.6rem;
  button,
  input {
    max-width: 70px;
    margin: 0;
    text-align: right;
  }
  input {
    padding-top: 1.4rem;
    padding-bottom: 1.4rem;
  }
  button {
    transition: 0.15s linear;
    &:active {
      background: @highlightColor;
    }
  }
}
.distance {
  position: relative;
}

.distance-title {
  text-transform: capitalize;
  font-size: 1.8rem;
  font-weight: 500;
}

.distance-ends {
  position: absolute;
  left: 0;
  font-size: 1.4rem;
  color: #999;
  bottom: -3rem;
}
</style>
